/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 1rem;
  background-color: #cecece;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  flex-flow: column;
  background-color: #fafbff;
  background-image: url(/images/pattern-circles.svg), url(/images/bg-pattern.svg);
  background-repeat: no-repeat, no-repeat;
  background-size: auto, 100% 30rem;
  background-position: 50% 70px, left top;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  border-radius: 10px;
  max-width: 540px;
  width: 100%;
  height: 400px;
  margin: 0 auto;
  margin-top: 5rem;
  box-shadow: 0 4px 21px 0px #00000026;
}
@media only screen and (max-width: 530px) {
  main {
    height: auto;
  }
}
main .packselection {
  flex: 1 0 60%;
  display: flex;
  flex-flow: column nowrap;
  padding: 2.875rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #bdccff;
}
@media only screen and (max-width: 530px) {
  main .packselection {
    padding: 2.875rem 0.875rem 2rem;
  }
}
main .packselection .pricing {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 530px) {
  main .packselection .pricing {
    flex-flow: column;
  }
}
main .packselection .pricing .price {
  display: flex;
  align-items: center;
}
main .packselection .pricing .price .dollars {
  margin-right: 5px;
}
main .packselection .sliderdiv {
  margin-top: 20px;
}
main .packselection .sliderdiv .slider {
  -webkit-appearance: none;
  /* Override default CSS styles */
  appearance: none;
  width: 100%;
  height: 8px;
  background: #eaeefb;
  outline: none;
  opacity: 0.7;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}
main .packselection .sliderdiv .slider:hover {
  opacity: 1;
}
main .packselection .sliderdiv .slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* Override default look */
  appearance: none;
  width: 40px;
  /* Set a specific slider handle width */
  height: 40px;
  /* Slider handle height */
  background: #10d5c2;
  /* Green background */
  cursor: pointer;
  /* Cursor on hover */
  border-radius: 50px;
  box-shadow: 1px 15px 25px 4px #a5f3eb;
  background-image: url(/images/icon-slider.svg);
  background-repeat: no-repeat;
  background-position: center;
}
main .packselection .sliderdiv .slider::-moz-range-thumb {
  width: 40px;
  /* Set a specific slider handle width */
  height: 40px;
  /* Slider handle height */
  background: #10d5c2;
  /* Green background */
  cursor: pointer;
  /* Cursor on hover */
  border-radius: 50px;
  box-shadow: 1px 15px 25px 4px #a5f3eb;
  background-image: url(/images/icon-slider.svg);
  background-repeat: no-repeat;
  background-position: center;
}
main .packselection .togglediv {
  margin-top: 50px;
  text-align: center;
  padding-left: 55px;
}
@media only screen and (max-width: 530px) {
  main .packselection .togglediv {
    padding-left: 0px;
  }
}
@media only screen and (max-width: 300px) {
  main .packselection .togglediv {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }
}
main .packselection .togglediv .switch {
  display: inline-block;
  height: 20px;
  position: relative;
  width: 40px;
  margin-left: 10px;
  margin-right: 10px;
}
@media only screen and (max-width: 300px) {
  main .packselection .togglediv .switch {
    transform: rotate(90deg);
    margin: 15px 0;
  }
}
main .packselection .togglediv .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
main .packselection .togglediv .switch.monthly > .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #cdd7ee;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 32px;
}
main .packselection .togglediv .switch.monthly > .slider::before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 4px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50px;
}
main .packselection .togglediv .switch.monthly > .slider:hover {
  background-color: #a5f3eb;
}
main .packselection .togglediv .switch.yearly > .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #cdd7ee;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 32px;
}
main .packselection .togglediv .switch.yearly > .slider::before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 4px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50px;
  -webkit-transform: translateX(18px);
  -ms-transform: translateX(18px);
  transform: translateX(18px);
}
main .packselection .togglediv .switch.yearly > .slider:hover {
  background-color: #a5f3eb;
}
main .packselection .togglediv .discount {
  background-color: #feece7;
  border-radius: 30px;
  margin-left: 10px;
  padding: 3px 10px;
}
@media only screen and (max-width: 530px) {
  main .packselection .togglediv .discount .larg {
    display: none;
  }
  main .packselection .togglediv .discount::after {
    content: "25%";
  }
}
main .cta {
  padding: 1rem 2.875rem;
  display: flex;
  flex-flow: row wrap;
  flex: 1 0 40%;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 530px) {
  main .cta {
    flex-flow: column;
  }
}
main .cta .specs ul {
  list-style-image: url(/images/icon-check.svg);
}
main .cta .specs li {
  margin-bottom: 10px;
}
main .cta button {
  width: 170px;
  height: 40px;
  background-color: #293356;
  border: none;
  border-radius: 30px;
  cursor: pointer;
}
@media only screen and (max-width: 530px) {
  main .cta button {
    margin-top: 10px;
  }
}

body {
  font-family: "Manrope", sans-serif;
  color: #000000;
  line-height: 1.3;
}

h1, h2, h3 {
  margin: 0px;
  color: #293356;
  text-align: center;
}

h1 {
  margin-top: 4.5rem;
}

p {
  color: #858fad;
  text-align: center;
}

span {
  color: #858fad;
  text-align: center;
}

.label {
  font-size: 12px;
}

.discount {
  font-size: 10px;
  color: #ff8c66;
}
.discount .larg {
  color: #ff8c66;
}

li {
  font-size: 12px;
  color: #858fad;
}

button {
